import React, { useRef, useEffect, useState } from 'react'
import locomotiveScroll from 'locomotive-scroll'
import Project from '../components/project/Project'
import Introduction from '../components/introduction/Introduction'
import About from '../components/about/About'

const Home = () => {
  const scrollRef = useRef(null)
  const [isLoaded, setIsLoaded] = useState(false)

  // useEffect(() => {
  //   if (scrollRef) {
  //     new locomotiveScroll({
  //       el: scrollRef.current,
  //       smooth: true,
  //       direction: 'horizontal',
  //     })
  //   }
  //   // return () => {
  //   //   scrollRef.destroy()
  //   // }
  // }, [])

  useEffect(() => {
    setTimeout(() => {
      setIsLoaded(true)
    })
  })

  useEffect(() => {
    let locomotiveScrollTemp
    if (isLoaded) {
      locomotiveScrollTemp = new locomotiveScroll({
        el: scrollRef.current,
        smooth: true,
        direction: 'horizontal',
      })
      locomotiveScrollTemp.update()
    }
    return () => {
      if (locomotiveScrollTemp) locomotiveScrollTemp.destroy()
    }
  }, [isLoaded])

  // let ls = new locomotiveScroll()

  return (
    <div className="scroll" ref={scrollRef} id="homeScroll" data-scroll-section>
      <div className="contentContainer">
        <Introduction />
        <Project />
        <About />
      </div>
    </div>
  )
}

export default Home
